import * as React from 'react';
import {NotificationPosition, ToastContainer} from '@here/hds-notification';

// eslint-disable-next-line no-new
new ToastContainer();

interface HDSToastContainerProperties extends React.HTMLAttributes<HTMLElement> {
  children?: React.ReactNode;
  position?: NotificationPosition;
}

const HDSToastContainer = React.forwardRef((props: HDSToastContainerProperties, ref) => {
  const {children, position = 'bottom-right', className, ...rest} = props;
  return (
    <hds-toast-container
      {...(position && {position})}
      {...(className && {class: className})}
      ref={ref}
      {...rest}
    >
      {children}
    </hds-toast-container>
  );
});

HDSToastContainer.defaultProps = {
  position: 'bottom-right',
};

export default HDSToastContainer;
